<template>
    <div class="header">
     
        <img src="../assets/og_Ms_logo.png" >
        <el-input class="InputDomo"
            size="mini"
            placeholder="请输入内容"
            prefix-icon="el-icon-search"
            suffix-icon="el-icon-error"
            v-model="input1" >
        </el-input>


    </div>
</template>


 <script>
 export default {
     data(){
         return{
             input1:''
         }
     }
 }
 </script>


 <style>
  .header{
      width: 60%;
      height: 80px;
      margin: 0 auto;
      /* display: flex; */
      
  }
  .header  img{
      height: 80px;
      width: 40%;
      float: left;
  }

  .InputDomo{
      
      width: 250px;
      float: right;
      margin: 10px 0;
      
      /* opacity: 0.6; */
      
  }

.el-input__inner{
    
      border-radius: 15px;
      background-color: #00000054;
      border-color: transparent;
      color: aliceblue;
      
  }

 </style>
 


